  /* Navigation bar styles */
  
  .navbar {
    margin-left: 1vh;
    width: 10vw;
    height: 95vh;
    background: red;
    background: #333333;
    border-radius: 1vh;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: white;
  }
  
  
  .nav-link-row{
    display: flex;
    flex-direction: row;
    margin: 1vh;
    padding: 1vh;
    text-decoration: none;
  }
  
  .nav-link-text{
    color: white;
    font-size: larger;
    
  }
  
  .nav-icon{
    margin-right: 1vh;
  }
  
  .nav-title{
    margin-left: 1vw;
    margin-top: 2vh;
    color: white;
    font-size: large;
  
    font-family: monospace;
    font-weight: bold;
  }
  
  .nav-link-row:hover ,.btn:hover{
    background-color: #6db46f;
    scale: 1.1;
  }
  
  .nav-link-row:active, .btn:active{
    background-color: #427944;
    scale: 1.025;
  }
  
  .active{
    background-color: white;
    color: black;
    font-weight: bolder;
  }
  
  .active:hover{
    background-color: white;
    scale: 1;
  }
  
  .active:active{
    background-color: white;
    scale: 1;
  }